<template>
  <v-container
    id="regular-forms"
    fluid
    tag="section"
  >

    <base-material-card
      color="success"
      inline
      title="企业添加"
      class="px-4 py-6"
      style=""
    >
      <!-- <v-subheader class="display-1 mt-3">
        Text fields
      </v-subheader> -->

      <v-form class="pt-1"  v-model="valid" :lazy-validation="lazy">

      <v-row
        align="center"
      >
        <v-col cols="12" sm="6">
          <v-text-field
            :rules="nameRules"
            required
            label="公司名称(*必填)"
            v-model="companyName"
            outlined
            class="ml-2"
          />
        </v-col>
        <v-col cols="12" sm="6">
          <v-text-field
          :rules="nameRules"
            required
            label="公司标识(*必填)"
            v-model="customerUniqueCode"
            outlined
            class="ml-2"
          />
        </v-col>
      </v-row>
        <v-row
        align="center"
      >
        <v-col cols="12" sm="6">
          <v-text-field
            :rules="numberRules"
            required
            label="客户数量(*必填)"
            v-model="customerNumber"
            outlined
            class="ml-2"
          />
          </v-col>
          <v-col cols="12" sm="6">
          <v-text-field
          :rules="numberRules"
            required
            label="购买天数(*必填)"
            v-model="purchaseDays"
            outlined
            class="ml-2"
          />
        </v-col>
      </v-row>
      <v-row
        align="center"
      >
        <v-col cols="12" sm="6">
          <v-text-field
          :rules="nameRules"
            required
            label="版本(*必填)"
            v-model="version"
            outlined
            class="ml-2"
          />
        </v-col>
      </v-row>
      <v-row
        align="center"
      >
        <v-col cols="12" sm="6">
          <v-text-field
            label="cropId(选填)"
            v-model="cropId"
            outlined
            class="ml-2"
          />
        </v-col>
        <v-col cols="12" sm="6">
          <v-text-field
            label="securte(选填)"
            v-model="securte"
            outlined
            class="ml-2"
          />
        </v-col>
      </v-row>
      <v-row
        align="center"
      >
        <v-col cols="12">
          <v-textarea
              outlined
              class="ml-2"
              name="input-7-4"
              label="公司信息(选填)"
              v-model="companyContent"
            />
        </v-col>
      </v-row>
      </v-form>
      <v-row  align="center" class="ml-1">
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" text @click="doCancel()">关闭</v-btn>

      <v-btn
        color="blue darken-1"
        :disabled="!valid"
        text
        @click="doSave()"
        >保存</v-btn>
          </v-row>
    </base-material-card>
  </v-container>
</template>

<script>
  export default {
    name: 'AddCompanyInfo',

    data: () => ({
    companyName:'',
    cropId:'',
    securte:'',
    customerNumber:'',
    companyContent:'',
    customerUniqueCode:'',
    purchaseDays:'',
    version:'',

    lazy: false,
    valid: true,
     //表单验证
    nameRules: [v => !!v || "该选项为必填字段"],
    numberRules:[(v => !!v || "该选项为必填字段"),(v => /^[0-9]*$/.test(v) || "该选项只能填数字" )]
    }),

    methods: {
     doSave(){
       let postData={company: {companyName:this.companyName,cropId:this.cropId,securte:this.securte,customerNumber:this.customerNumber,
       companyContent:this.companyContent,customerUniqueCode:this.customerUniqueCode,purchaseDays:this.purchaseDays},version:this.version};
        this.$http
        .post("/user/api/v1/company/addCompany", postData)
        .then(response => {
          if (response.success) {
            response.msg = "企业添加成功";
             this.$store.commit("SET_SNACK", response);
             this.$router.push({path:'companyManage'});
          }else{
             this.$store.commit("SET_SNACK", response);
          }
         
        })
        .catch(error => {
          console.log(error);
        });
     },
     doCancel(){
       this.$router.push({path:'companyManage'});
     }
    },
  }
</script>
